.container {
  max-width: 1244px;
}

.about_author {
  display: flex;
  justify-content: center;
  background-color: #FFFFFA;
}
.about_author .container {
  display: flex;
  justify-content: space-between;
  min-width: 1244px;
  padding: 80px 0;
}
.about_author__avatar {
  margin-left: 70px;
  height: 350px;
  width: 350px;
  background-color: #5B605F;
}
.about_author__description {
  margin-right: 60px;
  width: 650px;
  font-family: "Open Sans", sans-serif;
  color: #4a4a49;
}
.about_author__description__title {
  margin-bottom: 25px;
  font-size: 2rem;
  font-weight: bold;
}
.about_author__description__text {
  font-size: 1.2rem;
  line-height: 25px;
}